import React, { Fragment, useEffect, useState } from 'react';
import { Menu, Button, Layout, Space, Dialog, Input, Form, Alert, Popup, Textarea } from 'tdesign-react';
import { AppIcon, CodeIcon, FileIcon, FolderIcon, UserIcon, ViewListIcon, MailIcon, RollbackIcon, SettingIcon } from 'tdesign-icons-react';
import { BlockDatabase } from '../core/block-database';
import { Block } from '../core/block';

const { Content, Footer, Aside } = Layout;
const { SubMenu, MenuItem } = Menu;
const { FormItem } = Form;

interface EditBlockDialogProps {
    blockId: string | undefined,
    onFinish: () => void,
    blockDatabase: BlockDatabase,
}

export function EditBlockDialog({ blockId, onFinish, blockDatabase }: EditBlockDialogProps) {
    const [inputStr, setInputStr] = useState('');
    const [confirmLoading, setConfirmLoading] = useState(false);
    const [editBlock, setEditBlock] = useState<Block | undefined>(undefined);

    useEffect(() => {
        if (blockId === undefined) {
            setEditBlock(undefined);
            return;
        }

        (async () => {
            var block = await blockDatabase.getBlockById(blockId);
            setInputStr(block.getContent());
            setEditBlock(block);
        })();
    }, [])

    const onConfirm = () => {
        console.log('点击了确认按钮');
        setConfirmLoading(true);
        let content = inputStr.trim();

        (async () => {
            let block = editBlock;
            if (block === undefined) {
                block = await blockDatabase.newBlock();
            }
            block.setContent(content);
            await blockDatabase.updateBlock(block);
            onFinish();
            setConfirmLoading(false);
        })();
    };
    const onDelete = () => {
        console.log('点击了删除按钮');
        setConfirmLoading(true);

        (async () => {
            let block = editBlock;
            if (block === undefined) {
                return;
            }
            await blockDatabase.deleteBlock(block);
            onFinish();
            setConfirmLoading(false);
        })();
    };
    const onClickCloseBtn = () => {
        console.log('点击了关闭按钮');
    };
    const onKeydownEsc = () => {
        console.log('按下了ESC');
    };
    const onClickOverlay = () => {
        console.log('点击了蒙层');
    };
    const handleClose = () => {
        console.log('关闭弹窗，点击关闭按钮、按下ESC、点击蒙层等触发');
        onFinish();
    };

    return <>
        <Dialog
            header={editBlock === undefined ? "新增Block" : "修改Block"}
            visible={true}
            // confirmOnEnter
            confirmBtn='保存'
            confirmLoading={confirmLoading}
            cancelBtn={editBlock === undefined ? false : "删除"}
            onCancel={onDelete}
            closeBtn={true}
            onClose={handleClose}
            onConfirm={onConfirm}
            onEscKeydown={onKeydownEsc}
            onCloseBtnClick={onClickCloseBtn}
            onOverlayClick={onClickOverlay}
            width='90%'
        >
            <Textarea
                placeholder=""
                value={inputStr}
                autosize={true}
                onChange={(value) => {
                    setInputStr(value);
                }}
            />
            {/* <Form
                labelAlign="right"
                layout="vertical"
                preventSubmitDefault={true}
                resetType="empty"
                showErrorMessage
            >
                <FormItem
                    initialData=""
                    label="content"
                // name="tel"
                >
                </FormItem>
            </Form> */}
        </Dialog>
    </>
}