import { Block } from "./block";
import { Filesystem } from "./file-system";
import { generateUUID } from "./misc/uuid";

export class BlockDatabase {
    private fs: Filesystem;
    // private id2block: Map<string, Block> = new Map<string, Block>();
    private id2block: Map<string, Promise<Block | undefined>> = new Map<string, Promise<Block | undefined>>();

    static readonly ALL_BLOCK_IDS_PATH = 'block-ids.json';

    constructor(fs: Filesystem) {
        this.fs = fs;
    }

    private async addBlock(block: Block): Promise<void> {
        if (this.id2block.has(block.id))
            throw 'dup block id';

        // todo: lock

        // const keysArray = Array.from(this.id2block.keys());
        const keysArray = Array.from(await this.getAllBlockIds());
        keysArray.push(block.id);
        const idsStr = JSON.stringify(keysArray);
        await this.fs.writeFileText(BlockDatabase.ALL_BLOCK_IDS_PATH, idsStr);

        const path = this.getBlockFilePath(block.id);
        await this.fs.writeFileText(path, block.toText());
    }

    public async newBlock(): Promise<Block> {
        const block = new Block();
        block.id = generateUUID();
        block.createTime = new Date();
        await this.addBlock(block);
        return block;
    }

    public async updateBlock(block: Block): Promise<void> {
        const path = this.getBlockFilePath(block.id);
        await this.fs.writeFileText(path, block.toText());
    }

    public async deleteBlock(block: Block): Promise<void> {
        const keysArray = Array.from(await this.getAllBlockIds());
        const idsStr = JSON.stringify(keysArray.filter(item => item !== block.id));
        await this.fs.writeFileText(BlockDatabase.ALL_BLOCK_IDS_PATH, idsStr);

        const path = this.getBlockFilePath(block.id);
        await this.fs.deleteFile(path);
    }

    public async queryBlocks(): Promise<void> {
        throw 'no impl';
    }

    public async getAllBlockIds(): Promise<string[]> {
        if (!await this.fs.isFileExists(BlockDatabase.ALL_BLOCK_IDS_PATH)) {
            return [];
        }

        const content = await this.fs.readFileText(BlockDatabase.ALL_BLOCK_IDS_PATH);
        return JSON.parse(content) as string[];
    }

    public async getBlockById(id: string): Promise<Block> {
        let blockPromise = this.id2block.get(id);
        if (blockPromise == undefined) {
            blockPromise = this.loadBlockById(id).catch(err => {
                this.id2block.delete(id);
                throw err;
            });
            this.id2block.set(id, blockPromise);
        }

        const block = await blockPromise;
        if (block == undefined) {
            throw new Error("can't find block id: " + id);
        }
        return block;
    }

    private async loadBlockById(id: string): Promise<Block | undefined> {
        console.log('loadBlockById start, id: ' + id);

        const path = this.getBlockFilePath(id);
        if (!await this.fs.isFileExists(path)) {
            return undefined;
        }

        const text = await this.fs.readFileText(path);

        let block = new Block();
        block.parseText(text);

        console.log('BlockDatabase.loadBlockById ' + id, text);

        if (block.id != id)
            throw 'block.id != id, ' + block.id + ", " + id + ", " + text;

        return block;
    }

    private getBlockFilePath(id: string): string {
        return 'block/' + id.substring(0, 2) + '/' + id;
    }
}