import { useState, useEffect } from 'react';
import { Menu, Button, Layout, Space, Alert, Divider, List, Link, message } from 'tdesign-react';
import { TfApp } from '../core/tfapp';
import { EditBlockDialog } from './EditBlockDialog';
import { Block } from '../core/block';

const { Content, Footer, Aside } = Layout;
const { ListItem, ListItemMeta } = List;
const { SubMenu, MenuItem } = Menu;

export function StartPage() {
    const [text, setText] = useState<string>('');
    const [blockIds, setBlockIds] = useState<string[]>([]);

    const [showEditBlockDialog, setShowEditBlockDialog] = useState(false);
    const [editBlock, setEditBlock] = useState<string | undefined>(undefined);

    const tfapp = TfApp.get();
    const blockDatabase = tfapp.getBlockDatabase();

    useEffect(() => {
        console.log('StartPage useEffect');
        (async () => {
            const allBlockIds = await blockDatabase.getAllBlockIds();
            setBlockIds(allBlockIds);
        })();
    }, []);

    return (
        <>
            <Space direction='vertical'>

                {/* <Divider>测试</Divider> */}

                <Button onClick={async () => {
                    let text = '';
                    await TfApp.get().getFs().writeFileText('test2.txt', 'abc');
                    text += 'write finish<br>';
                    setText(text);

                    const exist = await TfApp.get().getFs().isFileExists('test2.txt');
                    console.log('isFileExists: ', exist);
                    text += 'exist: ' + exist + '<br>';
                    setText(text);

                    const content = await TfApp.get().getFs().readFileText('test2.txt');
                    console.log('readFile return: ', content);
                    text += 'content: ' + content + '<br>';
                    setText(text);

                    await TfApp.get().getFs().deleteFile('test2.txt');
                }}>
                    test
                </Button>
                {/* <Space
                    direction="horizontal"
                    size="medium"
                    breakLine
                >
                </Space> */}
                {text != '' ? <Alert theme="info" message={text} /> : null}

                <Button onClick={async () => {
                    setEditBlock(undefined);
                    setShowEditBlockDialog(true);
                }}>
                    新增Block
                </Button>
                {showEditBlockDialog
                    ? <EditBlockDialog
                        blockId={editBlock}
                        onFinish={() => { setShowEditBlockDialog(false); }}
                        blockDatabase={blockDatabase} />
                    : null}

                {/* <Divider>block list</Divider> */}

                <List split>
                    {
                        blockIds.map(id =>
                            <ListItem
                                key={id}
                                action={
                                    <Space>
                                        {/* <Link theme="primary" hover="color">
                                            查看
                                        </Link> */}
                                        <Link theme="primary" hover="color" onClick={() => {
                                            setEditBlock(id);
                                            setShowEditBlockDialog(true);
                                        }} >
                                            修改
                                        </Link>
                                        {/* <Link theme="danger" hover="color">
                                            删除
                                        </Link> */}
                                    </Space>
                                }
                            >
                                {/* <ListItemMeta title="" description="列表内容列表内容" /> */}
                                <ListItemMeta title={id} />
                            </ListItem>
                        )
                    }
                </List>
            </Space>
        </>
    );
}
